import React from 'react';
//import bg from './../Image/starTrek.jpg';
import './homePage.css';
import NavigationBar from './../container/NavigationBar';
import songList from './../Music/Music.json';
import Album from './../container/MusicPlayerWithAlbum.js';
import './musicPage.css';
class MusicPage extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="MusicPage" >
                <NavigationBar isLogo="false" />
                <div className="music_title">
                    Title: {this.getMusicTitle()}
                </div>
                <div className="sub">
                    Album: {this.getMusicAlbum()}
                </div>
                <div className="soon">
                    Music Page Release Soon
                </div>
            </div>
        );
    }
    getMusicTitle() {
        var music = songList[this.props.state.index];
        return music.title;
    }
    getMusicAlbum() {
        var music = songList[this.props.state.index];
        return music["album name"];
    }
    setSong() {
        var music = songList[this.props.state.index];
        var musicAddress = this.props.state.songlist[music.path];
        return musicAddress;
    }

}
export default MusicPage;